@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

h2 {
    font-size : 64px;
}

#login {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    width: 800px;
}

input {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 80px;
  border: solid 1px #dadada;
  background: #fff;
  box-sizing: border-box;
}

#btn {
font-family: "Nanum Gothic Script";
font-weight: bolder;
  font-size: 40px;
  display: block;
  width: 100%;
  height: 80px;
  margin: 40px 0px;
  cursor: pointer;
  text-align: center;
  color: #000;
  border: none;
  border-radius: 0;
  background-color: #ffe943;
}

label {
    font-family: "Nanum Gothic Script";
font-weight: bolder;
  font-size: 44px;
}