@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
font-family: "Nanum Gothic Script";
background-color: lightgoldenrodyellow;
line-height: 1.5;
font-weight: bolder;
}

header {
 text-align: center;
 font-size: 100px;
 margin-bottom: 50px;
}

.container {
 display: grid;
 grid-template-columns: 450px 1fr;
}

nav {
 font-family: "Nanum Gothic Script";
 margin-left: 100px;
 font-size: 40px;
}

main{
font-family: "Nanum Gothic Script";
margin-left: 300px;
background-color: lightgoldenrodyellow;
font-size: 70px;
}

footer {
 text-align: center;
 background-color: rgb(197, 212, 87);
 color: white;
 font-size: 3em;
 margin-top: 100px;
}

/*여기가 메뉴 버튼*/
a:link, a:visited {
 background-color:#ffe943; 
 padding: 10px 20px;
 text-decoration: none;
 display: inline-block;
 width:450px;
 font-size: 1.5em;
margin: 5px;
color: black ;
}
  
a:hover, a:active { 
 background-color: #f2ff00; 
}

button {
    font-family: "Nanum Gothic Script";
    background-color: rgb(250, 178, 54);
    color: white;
    padding: 10px 20px;
    margin: 5px;
    font-size: 1em;
    cursor: pointer;
    display: inline-block;
    border: none;
  }
  
  button:hover {
    background-color: orange;
    color: white;
  }

  input {
    width:200px;
    height: 80px;
    font-size:60px;
  }
  
  
  
